
/* header */

.headerLoginSignup{
	background-color:#262626;
	box-shadow:0 1px 1px #ccc;
	padding: 20px 40px;
	height: 80px;
	color: #ffffff;
	box-sizing: border-box;
}

.headerLoginSignup .headerLimiter {
	max-width: 1200px;
	text-align: center;
	margin: 0 auto;
}

/* Logo */

.headerLoginSignup .headerLimiter h1{
	float: left;
	font: normal 28px Cookie, Arial, Helvetica, sans-serif;
	line-height: 40px;
	margin: 0;
}

.headerLoginSignup .headerLimiter h1 span {
	color: #e74c3c;
}

/* The navigation links */

.headerLoginSignup .headerLimiter a {
	color: #ffffff;
	text-decoration: none;
}

.headerLoginSignup .headerLimiter nav {
	font:16px Arial, Helvetica, sans-serif;
	line-height: 40px;
	float: left;
	margin: 0 0 0 60px;
	padding: 0;
}

.headerLoginSignup .headerLimiter nav a{
	font-size: 14px;
	display: inline-block;
	padding: 0 5px;
	opacity: 0.9;
	text-decoration:none;
	line-height: 1;
}

.headerLoginSignup .headerLimiter nav a:hover {
	opacity: 1;
}

.headerLoginSignup .headerLimiter nav a.selected {
	color: #608bd2;
}

/* Login/Sign up buttons */

.headerLoginSignup .headerLimiter ul {
	font: 14px Arial, Helvetica, sans-serif;
	list-style: none;
	line-height: 1;
	float: right;
	margin-top: 15px;
}

.headerLoginSignup .headerLimiter ul li {
	display: inline-block;
	margin-left: 15px;
	opacity:0.9;
}

.headerLoginSignup .headerLimiter ul li:hover{
	opacity: 0.5;
}

/* The sign up button */

.headerLoginSignup .headerLimiter ul li:last-child a {
	font-weight: bold;
	background-color: #e74c3c;
	padding: 10px 15px;
	border-radius: 3px;
}

/* Making the header responsive */

@media all and (max-width: 600px) {

	.headerLoginSignup {
		padding: 25px;
		height: 85px;
	}

	.headerLoginSignup .headerLimiter h1 {
		float: none;
		margin: -8px 0 2px;
		text-align: center;
		font-size: 24px;
		line-height: 1;
	}

	.headerLoginSignup .headerLimiter nav {
		margin: 0;
		float: none;
	}

	.headerLoginSignup .headerLimiter nav li a {
		font-size: 13px;
	}

	.headerLoginSignup .headerLimiter ul {
		display: none;
	}

}


