  .btn{
    z-index: 3;
    background: transparent !important;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    text-decoration:none;
    color:#fff;
    letter-spacing:1px;
    font-size:2rem;
    box-sizing:border-box;
  }
  .btn span{
    position:relative;
    box-sizing:border-box;
    display:flex;
    font-size: 25px;
    align-items:center;
    justify-content:center;
    width:200px;
    height:200px;
  }
  .btn span:before{
    content:'';
    width:100%;
    height:100%;
    display:block;
    position:absolute;
    border-radius:100%;
    border:7px solid #1ebea5;
    box-sizing:border-box;
    transition: all .85s cubic-bezier(0.25, 1, 0.33, 1);
    box-shadow: 0 30px 85px rgba(0,0,0,0.14), 0 15px 35px rgba(0,0,0,0.14);
  }
  .btn:hover span:before{
    transform:scale(0.8);
    box-shadow: 0 20px 55px rgba(0,0,0,0.14), 0 15px 35px rgba(0,0,0,0.14);
  }
  .btn .dots-container{
    opacity:0;
    -webkit-animation: intro 1.6s;
            animation: intro 1.6s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }
  .btn .dot{
    width:8px;
    height:8px;
    display:block;
    background-color:#F3CF14;
    border-radius:100%;
    position:absolute;
    transition: all .85s cubic-bezier(0.25, 1, 0.33, 1);
  }
  .btn .dot:nth-child(1){
    top:50px;
    left:50px;
    transform:rotate(-140deg);
    -webkit-animation: swag1-out 0.3s;
            animation: swag1-out 0.3s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    opacity:0;
  }
  .btn .dot:nth-child(2){
    top:50px;
    right:50px;
    transform:rotate(140deg);
    -webkit-animation: swag2-out 0.3s;
            animation: swag2-out 0.3s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    opacity:0;
  }
  .btn .dot:nth-child(3){
    bottom:50px;
    left:50px;
    transform:rotate(140deg);
    -webkit-animation: swag3-out 0.3s;
            animation: swag3-out 0.3s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    opacity:0;
  }
  .btn .dot:nth-child(4){
    bottom:50px;
    right:50px;
    transform:rotate(-140deg);
    -webkit-animation: swag4-out 0.3s;
            animation: swag4-out 0.3s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    opacity:0;
  }
  .btn:hover .dot:nth-child(1){
    -webkit-animation: swag1 0.3s;
            animation: swag1 0.3s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }
  .btn:hover .dot:nth-child(2){
    -webkit-animation: swag2 0.3s;
            animation: swag2 0.3s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }
  .btn:hover .dot:nth-child(3){
    -webkit-animation: swag3 0.3s;
            animation: swag3 0.3s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }
  .btn:hover .dot:nth-child(4){
    -webkit-animation: swag4 0.3s;
            animation: swag4 0.3s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
  }
